            return {
                title: '对公业务',
                ins: 0,
                list: [],
                isNoMoreData: false,
                busy: false,
                search: {
                    page: 1,
                    pageSize: 15,
                    did: this.$route.query.did
                }
            };
        },
        created() {
            this.getArticleList();
        },
        methods: {
            changTab(str) {
                this.ins = str;
            },
            goto(str) {
                window.location.href = str;
            },
            loadMore() {
                this.busy = true;
                this.getArticleList();
            },
            getArticleList() {
                console.log(this.search.page, this.list.length)
                mockData.forEach(el => {
                    this.list.push(el);
    
    
                });
                this.busy = false;
                this.search.page++;
    
                return false;
                let url = '/youyi/common/getArticle';
                let params = this.search;
                this.$http({
                    method: 'get',
                    url,
                    params
                }).then(res => {
                    let list = res.data.articleInfo || [];
                    if (list.length) {
                        list.forEach(item => {
                            this.list.push(item);
                        });
                        this.busy = false;
                        this.search.page++;
                    } else {
                        this.isNoMoreData = true;
                    }
                });
    
            }
    
        }
    };
