
<template>
    <section class="nav">
         <div class="container">
            <div class="c-row">
            <div class="c-span5" :class="{active:0==ins}">
             <router-link to="index">
                <div class="icons"><i class="icon iconfont icon-bottom_home_effect"></i></div>
                <p>首页</p>
               </router-link> 
            </div>
            <div class="c-span5"  :class="{active:1==ins}">
            <router-link to="service">
                <div class="icons"><i class="icon iconfont icon-bottom_service_effec"></i></div>
                <p>服务</p>
              </router-link> 
            </div>
            <div class="c-span5"  :class="{active:2==ins}">
