         </router-link> 
            </div>
            <div class="c-span5"  :class="{active:3==ins}">
              <router-link to="news">
                <div class="icons"><i class="icon iconfont icon-bottom_news"></i></div>
                <p>新闻</p>
                 </router-link>
            </div>
            <div class="c-span5"  :class="{active:4==ins}">

              <router-link to="guide">
                <div class="icons"><i class="icon iconfont icon-bottom_guide_effect"></i></div>
                <p>指南</p>
             </router-link>
            </div>
        </div>
         </div>
