        components: {},
        data() {
            return {};
        },
        created() {},
        methods: {
            loadMore() {},
            clickfn(num){
                this.ins = num;
            }
        }
    };
</script>

<style scoped lang='less'>
  
    .nav {
        position: fixed;
        background:#fff;
        bottom:0;
        width:100%;
         a{
                display:block;
                width:100%;
                height:100%;
                
            }
       .container {
            padding: 5px 13px 0px 13px;
           
       }
        .icons {
            width: 22px;
            height: 22px;
            margin: 0px auto;
            background: #fff;
            text-align: center;
            i {
                font-size: 22px;
                color: #999;
            }
        }
        p {
            color: #999;
            font-size: 10px;
            text-align: center;
            line-height:23px;
        }
    }
    .active{
         .icons {
            width: 22px;
            height: 22px;
            margin: 0px auto;
            background: #fff;
            text-align: center;
            i {
                font-size: 22px;
                color: #1b6ed4;
            }
        }
         p {
          color: #1d8fe1;
            font-size: 10px;
            text-align: center;
        }
    }
    .nav:after {
        content: "  ";
        position: absolute;
        left: 0;
        top: 0px;
        width: 100%;
        height: 1px;
        background-color: #ebeef0;
        -webkit-transform: scaleY(.5);
        transform: scaleY(.5);
    }
</style>



