<template>
    <div class="header-search">
        <router-link to="search">
            <div class="header-left">
                <i class="icon iconfont  icon-other_search"></i>
            </div>
        </router-link>
        <div class="c-title c-center">
            <div class="c-line-clamp1">
                <slot></slot>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'search-header',
        props: ['left'],
        data() {
            return {};
        },
        methods: {
    
            /**
             * 左上角返回按钮，配置了left的值，则跳转到left，否则返回
             */
            searchClick() {
                alert('go')
            },
    
            /**
             * 右上角按钮，会向父元素触发 rightClick 事件
             */
            rightClick() {
                this.$emit('rightClick');
            }
        }
    };
</script>

<style lang="less" scoped>
    .header-search {
        height: 42px;
        background: url('../assets/img/headbj.png');
        background-size: 100% 100%;
        position: relative;
        .header-left {
            position: absolute;
            right: 0;
            top: 0;
            width: 50px;
            line-height: 40px;
            i {
                margin-left: 17.5px;
                font-size: 17px;
                color: #fff;
            }
        }
        .c-title {
            line-height: 40px;
            color: #fff;
        }
    }
</style>


